html {
  font-weight: 400;
  line-height: 24px;
  font-style: normal;
  font-size: 16px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #2D383F;
  background-color: #FFFFFF;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
