body {
  position: relative;
}

.header {
  background-color: #0266C1;
  display: grid;
  grid-template-columns: 205px 1fr 145px;
  max-width: 1200px;
  margin: 0 auto;
  align-items: center;
  padding: 32px 20px;
  z-index: 100;
  position: relative;
}

.header__burger {
  display: none;
}

.header__phone {
  color: #FFFFFF;
  font-size: 18px;
  font-weight: 600;
  line-height: 18px; /* 100% */
  transform: translate(0, 2px);
}
.header__phone:hover {
  color: #1C3374;
  transition: color 0.15s ease;
}
.header__phone:active {
  color: rgba(255, 255, 255, 0.5);
  transition: none;
}
.header__phone:disabled, .header__phone--disabled {
  color: #FFFFFF;
}

.header__logo {
  width: 205px;
  height: 62px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navigation__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0;
  padding: 0 20px;
  justify-content: center;
  align-items: center;
  gap: 11px;
  transform: translateY(2px);
}

.navigation__item {
  list-style-type: none;
}

.navigation__link {
  color: #FFFFFF;
  padding: 10px;
  transition: color 0.3s ease;
}
.navigation__link:hover {
  color: #1C3374;
  transition: color 0.15s ease;
}
.navigation__link:active {
  color: rgba(255, 255, 255, 0.5);
  transition: none;
}
.navigation__link:disabled, .navigation__link--disabled {
  color: #FFFFFF;
}

.hero {
  width: 100vw;
  position: absolute;
  top: 0;
  min-height: 800px;
}

.hero__slider {
  margin: 0;
  padding: 0;
  height: 100%;
  min-height: 800px;
  list-style: none;
}

.hero__slide {
  height: 100%;
  min-height: 800px;
}

.hero__img-wrapper {
  position: absolute;
  height: 100%;
}
.hero__img-wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(112deg, rgba(15, 20, 41, 0.33) 17.23%, rgba(15, 20, 41, 0) 94.93%);
}
.hero__img-wrapper img {
  position: relative;
  width: 100vw;
  height: 800px;
  height: 100%;
  object-fit: cover;
}

.hero__content {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 200px 0 79px;
  color: #FFFFFF;
  height: 100%;
  min-height: 800px;
}
.hero__content h3 {
  margin: 0;
  width: 944px;
  margin-bottom: 23px;
  position: relative;
  z-index: 1;
  font-size: 77px;
  font-weight: 800;
  line-height: 77px; /* 100% */
}
.hero__content p {
  width: 464px;
  margin: 0;
  margin-bottom: 37px;
}

.hero__view-tour-btn {
  margin-bottom: auto;
}

.hero__features {
  display: flex;
  gap: 8px;
}

.hero__extra {
  position: absolute;
  right: 0;
}
.hero__extra--slide-1 {
  top: 327px;
}
.hero__extra--slide-1 img {
  width: 482px;
}
.hero__extra--slide-2 {
  width: 410px;
  height: 459px;
  bottom: 154px;
}

.hero .hero__pagination {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  padding: 0 120px;
  padding-left: 600px;
  width: 1440px;
  z-index: 100;
  bottom: 97px;
  left: 50%;
  transform: translateX(-50%);
  text-align: right;
  display: flex;
  justify-content: flex-end;
  gap: 20px;
}

.hero__pagination .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  border: 1px solid #FFFFFF;
  opacity: 1;
  background: transparent;
  transition: background 0.3s ease;
}
.hero__pagination .swiper-pagination-bullet.swiper-pagination-bullet {
  margin: 0;
}
.hero__pagination .swiper-pagination-bullet:hover {
  background-color: rgba(255, 255, 255, 0.5);
}
.hero__pagination .swiper-pagination-bullet:focus-visible {
  outline: none;
  outline: 1px solid rgba(255, 255, 255, 0.5);
  outline-offset: 2px;
}
.hero__pagination .swiper-pagination-bullet-active {
  background: #FFFFFF;
}

.tours {
  margin-top: 800px;
}

.btn {
  display: inline-flex;
  padding: 20px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  color: #FFFFFF;
  text-align: center;
  justify-content: center;
  font-weight: 500;
  line-height: 16px; /* 100% */
  background-color: #0266C1;
  min-width: 180px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}
.btn:hover:not(:disabled):not(.btn--disabled), .btn:focus-visible:not(:disabled):not(--disabled) {
  outline: none;
  color: #1C3374;
  background-color: #FFFFFF;
  border-color: #1C3374;
  border-radius: 5px;
  transition: all 0.05s ease-in-out;
  transition: background-color 0.15s ease, color 0.15s ease, border-color 0.15s ease;
}
.btn:active:not(:disabled):not(.btn--disabled) {
  color: #FFFFFF;
  background-color: #1C3374;
  transition: background-color 0.15s ease, color 0.15s ease, border-color 0.15s ease;
}
.btn:disabled, .btn--disabled {
  background-color: #999999;
}

.btn-play {
  position: absolute;
  display: flex;
  width: 56px;
  height: 56px;
  border: 1px solid #FFFFFF;
  padding: 0;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  color: #FFFFFF;
  background-color: transparent;
  transition: color 0.3s ease, border-color 0.3s ease;
}
.btn-play:hover, .btn-play:focus-visible {
  border-color: #1C3374;
  color: #1C3374;
  outline: none;
}
.btn-play:active:not(:disabled) {
  background-color: #1C3374;
}
.btn-play:disabled {
  border-color: rgba(2, 102, 193, 0.1);
  color: #0266C1;
}
.btn-play:disabled svg path {
  fill: #0266C1;
}

.feature {
  display: inline-flex;
  padding: 14px 20px;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

.feature--hero {
  border: 1px solid rgba(255, 255, 255, 0.5);
}

